@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700;800&family=Shrikhand&display=swap);
body{
background-color: #363844;
margin: 0;
}

h2{
    margin: 10px;
}

.title{
    margin-left: auto;
    margin-right: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #fb6501;
    font-size: 24px;
    border: 2px solid #fb6501;
    margin-bottom: 50px;
}
.banner{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    grid-gap: 2%;
    gap: 2%;

    margin-right: 100px;
    margin-bottom: 50px;
    height: 120px;
}
.banner_background{
    background-color: #23232a;
    width: 100%;
    box-shadow: 0 6px 10px #0e0e12;
}

.banner a{
    text-decoration: none;
    color: inherit;
    font-size: 18px;
    font-weight: bold;
    color: white;
    text-shadow: gray;
}

@media(max-width:900px){
    .banner {
        width: 100%;
    }
    .banner a {
        margin-right: auto;
        margin-left: auto;
        font-size: 14px;
    }
}
*{
    font-family: 'Montserrat', sans-serif;
}

ul{
    list-style: none;
    color: #fb6501;
    font-weight: bold;
    
    display: flex;
    grid-gap: 50px;
    gap: 50px;
    justify-content: flex-end;
    margin-right: 50px;
    font-size: 24px;
}
.prenom{
    color: white;
}
nav{
    display: flex;
    align-items: center;

}
.name{
    color: #fb6501;
    display: flex;
    font-size: 34px;
    grid-gap: 25px;
    gap: 25px;
    position:relative;
    margin-bottom: 0;
}
a{
    text-decoration: none;
    list-style: none;
}
h1{
    margin-bottom: 0;
}
.developpeur{
    color: white;
    font-size: 36px;
    margin-left: auto;
    margin-right: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.presentation{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;
}
@media (max-width:900px) {
    .developpeur{
        font-size: 24px;
    }
    .name{
        font-size: 28px;
    }
    .title{
        text-align: center;
    }
}
.link_left{
    position: -webkit-sticky;
    position: sticky;
    z-index: -1;
    bottom: 20px;
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
    margin-left: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    z-index: 10;
}
.link_left ul{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 0;
}
.link_left ul li{
    position: relative;
    list-style: none;
}
.link_left ul li a{
    position: relative;
    text-decoration: none;
}

img{
    width: 30px;
}
.link_list{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0;
    margin-left: 10px;
}

@media(max-width : 900px){
    .link_left{
        flex-direction: row;
        position:static;
        margin-left: auto;
        margin-right: auto;
    }
    .link_list{
        flex-direction: row;
        margin-right: auto;
        margin-left: auto;
        padding: 0;
        margin: 0;
        margin-bottom: 20px;
    }
}

.global_skills{
    margin:  50px 80px  ;
}
.Skills{
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}
h3,li {
    color: white;
}
strong{
    text-transform: uppercase;

}
h3{
    font-size: 20px;
    text-transform: uppercase;
}
ul{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}
.skill_list{
    list-style: square;
}
li{
    font-size: 14px;
}
p{
    font-style: italic;
    font-size: 12px;
    margin-top: 5px;
    font-weight: 100;
}
.dev{
    margin-bottom: 20px;
    margin-left: 0px;
}
.Pole_skills{
    margin-right: 80px;
}

@media(max-width:900px){
    .global_skills{
        margin: 0 20px;
    }
    .Skills{
        flex-direction: column;
    }
    .Pole_skills{
        /* border: 2px solid white; */
        margin-bottom: 35px;
        margin-left: auto;
        margin-right: auto;
        width: 300px ;
    }
    .Pole_skills h3{
        margin-left: 30px;
        margin: 0;
        margin-bottom: 30px;
        font-size: 18px;
    }
    .Pole_skills ul{
        margin: 0px;
        padding: 0;
        /* list-style: nonje; */
    }
    .dev {
        margin: 0;
        padding: 0;
        width: 100%;
        text-align: center;
    }
    .dev_skills h3{
        text-align: center;
        font-size: 17px;
    }
    .Outils{
        margin-top: 50px;
        margin-bottom: 100px;
    }
    .Outils h3{
        font-size: 18px;
        text-align: center;
    }
    .Outils ul{
        padding: 0;
        margin-right: auto;
        text-align: center;
    }
}
.about{
    color: White;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    text-align: center;
}
.about a {
    color: inherit;
    text-decoration: underline;
}
.section_about{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: auto;
    margin-left:auto;
}
.Apropos{
    padding-bottom: 50px;
    margin: 0 300px;
}

@media (max-width: 900px) {
    .Apropos{
        width: 80%;
        margin: 40px;
        margin-left: auto;
        margin-right: auto;
    }
    .about{
        line-height: 30px;
    }
}
.cards{
    background-color: white;
    width: 50%;
    height: 400px;
    
    padding: 20px;
    margin-bottom: 50px;
    margin-right: auto;
    margin-left: auto;

    display: flex;
    flex-direction: row;
    grid-gap: 5%;
    gap: 5%;

    border-radius: 25px;
}
.cards a{
    width: 80%;
}
.bckg_image{
    height: 100%;
}
.pictures{
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: 0 0 10px rgb(110, 110, 110) ;
}

.text{
    width: 33%;
}

.projet_title{
    margin-left: auto;
    margin-right: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.booki{
    color:  #0065FC;
}
.ohmyfood{
    color: #9356DC;
}
.Kasa{
    color: #ff6060;
}
.projet_pic{
    width: 100%;
    height: auto;
}
.projet_description{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: rgb(108, 108, 108);

    text-align: center;
    font-size: 18px;
    font-weight: bold;
}
.tags{
    display: flex;
    flex-direction: row;
    grid-gap: 10%;
    gap: 10%;

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    margin-right: auto;
    margin-left: auto;
}
.tag{
    background-color: white;
    box-shadow: 0 0 10px rgb(110, 110, 110) ;
    padding: 5px 15px;

    font-weight: bold;
    color: #fb6501;
}
@media(max-width: 900px){
    .cards{
        width: 80%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        padding: 0;
        margin: 0;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 40px;

        display: flex;
        flex-direction: column;
        padding: 20px;
        
    }
    .cards a {
        width: 100%;
        
    }
    .pictures{
        height: 200px;
        width: 100%;
    }

    .text{
        width: 100%;
        z-index: 5;
    }
}
.Footer{
height:200px;
background-color: #23232a;
margin-bottom: 0;

}

.contact{
    padding-top: 40px;
    text-align: center;
    font-size: 22px;
    color: white;
}

.contact a {
    color: inherit;
    font-size: 22px;
    font-weight: bold;
}
@media(max-width: 900px){
    .contact{
        margin: 0 20px;
        font-size: 18px;
    }
}


